.LiveFooter_header__2JLbl {
    position:relative;
    text-align:center;
    background: linear-gradient(60deg, rgb(0, 255, 191) 0%, rgb(19, 96, 77) 100%);
    color:white;
    margin-top: 1rem;
    /*padding: 1rem;*/
    bottom: 0;
    left: 0;
    width: 100%;
}
.LiveFooter_footerLogo__5rqQX {
    width:50px;
    fill:white;
    padding-right:15px;
    display:inline-block;
    vertical-align: middle;
}

.LiveFooter_inner-header__2GcYp {
    height:65vh;
    width:100%;
    margin: 0;
    padding: 0;
}

.LiveFooter_flex__1HYR4 { /*Flexbox for containers*/
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
}

.LiveFooter_waves__2ZycQ {
    position:relative;
    width: 100%;
    height:10vh;
    margin-bottom:-7px; /*Fix for safari gap*/
    min-height:100px;
    max-height:150px;
}

.LiveFooter_content__1NNE6 {
    position:relative;
    height:5vh;
    text-align: center;
    background-color: white;
}

/* Animation */

.LiveFooter_parallax__3LXpr > use {
    -webkit-animation: LiveFooter_move-forever__3z0db 25s cubic-bezier(.55,.5,.45,.5)     infinite;
            animation: LiveFooter_move-forever__3z0db 25s cubic-bezier(.55,.5,.45,.5)     infinite;
}
.LiveFooter_parallax__3LXpr > use:nth-child(1) {
    -webkit-animation-delay: -2s;
            animation-delay: -2s;
    -webkit-animation-duration: 7s;
            animation-duration: 7s;
}
.LiveFooter_parallax__3LXpr > use:nth-child(2) {
    -webkit-animation-delay: -3s;
            animation-delay: -3s;
    -webkit-animation-duration: 10s;
            animation-duration: 10s;
}
.LiveFooter_parallax__3LXpr > use:nth-child(3) {
    -webkit-animation-delay: -4s;
            animation-delay: -4s;
    -webkit-animation-duration: 13s;
            animation-duration: 13s;
}
.LiveFooter_parallax__3LXpr > use:nth-child(4) {
    -webkit-animation-delay: -5s;
            animation-delay: -5s;
    -webkit-animation-duration: 20s;
            animation-duration: 20s;
}
@-webkit-keyframes LiveFooter_move-forever__3z0db {
    0% {
        -webkit-transform: translate3d(-90px,0,0);
                transform: translate3d(-90px,0,0);
    }
    100% {
        -webkit-transform: translate3d(85px,0,0);
                transform: translate3d(85px,0,0);
    }
}
@keyframes LiveFooter_move-forever__3z0db {
    0% {
        -webkit-transform: translate3d(-90px,0,0);
                transform: translate3d(-90px,0,0);
    }
    100% {
        -webkit-transform: translate3d(85px,0,0);
                transform: translate3d(85px,0,0);
    }
}
/*Shrinking for mobile*/
@media (max-width: 768px) {
    .LiveFooter_waves__2ZycQ {
        height:40px;
        min-height:40px;
    }
    .LiveFooter_content__1NNE6 {
        border-color: aquamarine;
        height:30vh;
    }
    h1 {
        font-size:24px;
    }
}
