$duration: 4s;
$delays: ();
$variance: 300;
$easing: cubic-bezier(.5, 0, 0, 1);
$gap: 2px;

@for $row from 1 through 3 {
  @for $col from 1 through 3 {
    @for $layer from 1 through 3 {
      $delays: map-merge($delays, ('#{$row}#{$col}#{$layer}': random($variance)));
    }
  }
}

