    max-width: 100px;
    border-radius: 50%;
    vertical-align: middle;
    border: 2px solid aquamarine !important;

}

.aboutPic {
    max-width: 550px;
    overflow: hidden;
    /*border-radius: 50%;*/
    border-color: aquamarine;
    /*border-style: solid;*/
    /*vertical-align: middle;*/
}

.selection h6 {
    color: green;
