/*
---------------------------------------------
about
---------------------------------------------
*/

.about {
    padding: 120px 0px 0px 0px;
    position: relative;
    border-bottom: 5px solid #beb9b9;

    /*z-index: 9;*/
}

.about .container {
    /*border-bottom: 5px solid #a44040;*/
    padding-bottom: 80px;
}

.sectionHeading {
    margin-bottom: 10px;
}

.sectionHeading h6 {
    color: #5fb759;
    text-transform: uppercase;
}

.serviceItem {
    margin-top: 30px;
}

.icon {
    float: left;
    margin-right: 15px;
    width: 46px;
    height: 46px;
}

.serviceItem img {

    float: left;
    margin-right: 15px;
    width: 46px;
    height: 46px;
}

.serviceItem h4 {
    font-size: 15px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.25px;
    line-height: 46px;
}

.rightTextContent {
    height: 500px;
    width: 100%;
    margin-top: 40px;
    max-width: 500px;
    /*border-radius: 50%;*/
    border-color: aquamarine;
    border-style: solid;
    /*vertical-align: middle;*/
    overflow: hidden;

}

.rightTextContent p {
    font-size: 16px;
    line-height: 27px;
    color: #777;
}

.mainButtonIcon {
    font-size: 10px !important;
    border-radius: 25px !important;
    padding: 15px 25px ;
    background-color: #5fb759 !important;
    text-transform: uppercase;
    color: #fff;
    font-weight: 600;
    letter-spacing: 1px;
    -webkit-transition: all 0.3s ease 0s;
    -moz-transition: all 0.3s ease 0s;
    -o-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
}

.mainButtonIcon i {
    margin-left: 5px;
    -webkit-transition: all 0.3s ease-in-out 0s;
    -moz-transition: all 0.3s ease-in-out 0s;
    -o-transition: all 0.3s ease-in-out 0s;
    transition: all 0.3s ease-in-out 0s;
}

.mainButtonIcon:hover i {
    padding-left: 5px;
}